import { FlowCallbacks, ManualSignupData } from '../types/auth.types';
import { apiPost } from '../utils/api.utils';

// Session storage keys
const STORAGE_KEY_SIGNUP = 'cc_signup_temp';

/**
 * Step 1: Start manual signup
 * Creates temporary signup record and sends email OTP
 */
export async function startSignup(
    data: ManualSignupData,
    callbacks: FlowCallbacks
): Promise<void> {
    const { name, email, password } = data;

    // Validate input
    if (!name || !email || !password) {
        callbacks.onError?.('All fields are required');
        return;
    }

    // Call backend
    const result = await apiPost('/api/auth/customer/signup/start', {
        name,
        email,
        password,
    });

    if (result.error) {
        // Handle specific error codes
        if (result.errorCode === 'EMAIL_EXISTS') {
            callbacks.onError?.('Email already registered. Please sign in.');
            return;
        }
        callbacks.onError?.(result.error);
        return;
    }

    // Store signup data temporarily
    sessionStorage.setItem(STORAGE_KEY_SIGNUP, JSON.stringify({ name, email, password }));

    // Navigate to email OTP verification
    callbacks.onNavigate?.('/customer/verify-otp', { email, intent: 'manual_signup' });
}

/**
 * Step 2: Verify email OTP
 */
export async function verifyEmailOtp(
    email: string,
    otp: string,
    callbacks: FlowCallbacks
): Promise<void> {
    if (!email || !otp) {
        callbacks.onError?.('Email and OTP are required');
        return;
    }

    const result = await apiPost('/api/auth/customer/signup/verify-email', {
        email,
        otp,
    });

    if (result.error) {
        if (result.errorCode === 'INVALID_OTP') {
            callbacks.onError?.('Invalid or expired OTP. Please try again.');
            return;
        }
        if (result.errorCode === 'SESSION_EXPIRED') {
            callbacks.onError?.('Signup session expired. Please start again.');
            // Clear storage
            sessionStorage.removeItem(STORAGE_KEY_SIGNUP);
            return;
        }
        callbacks.onError?.(result.error);
        return;
    }

    // Navigate to mobile number collection
    callbacks.onNavigate?.('/customer/verify-mobile', { email, intent: 'manual_signup' });
}

/**
 * Step 3: Submit mobile number and request mobile OTP
 */
export async function submitMobile(
    email: string,
    mobile: string,
    callbacks: FlowCallbacks
): Promise<void> {
    if (!email || !mobile) {
        callbacks.onError?.('Email and mobile number are required');
        return;
    }

    const result = await apiPost('/api/auth/customer/signup/add-mobile', {
        email,
        mobile,
    });

    if (result.error) {
        if (result.errorCode === 'MOBILE_EXISTS') {
            callbacks.onError?.('Mobile number already registered');
            return;
        }
        if (result.errorCode === 'EMAIL_NOT_VERIFIED') {
            callbacks.onError?.('Email not verified. Please verify your email first.');
            return;
        }
        if (result.errorCode === 'SESSION_EXPIRED') {
            callbacks.onError?.('Signup session expired. Please start again.');
            sessionStorage.removeItem(STORAGE_KEY_SIGNUP);
            return;
        }
        callbacks.onError?.(result.error);
        return;
    }

    // Update storage with mobile
    const signupData = sessionStorage.getItem(STORAGE_KEY_SIGNUP);
    if (signupData) {
        const data = JSON.parse(signupData);
        sessionStorage.setItem(STORAGE_KEY_SIGNUP, JSON.stringify({ ...data, mobile }));
    }

    // Stay on same page or navigate to mobile OTP verification
    // (UI will show OTP input field)
    callbacks.onNavigate?.('/customer/verify-mobile', {
        email,
        mobile,
        intent: 'manual_signup',
        step: 'verify-otp'
    });
}

/**
 * Step 4: Verify mobile OTP and finalize signup
 */
export async function verifyMobileOtp(
    email: string,
    mobile: string,
    otp: string,
    callbacks: FlowCallbacks
): Promise<void> {
    if (!email || !mobile || !otp) {
        callbacks.onError?.('Email, mobile, and OTP are required');
        return;
    }

    // First, verify mobile OTP
    const verifyResult = await apiPost('/api/auth/customer/signup/verify-mobile', {
        email,
        mobile,
        otp,
    });

    if (verifyResult.error) {
        if (verifyResult.errorCode === 'INVALID_OTP') {
            callbacks.onError?.('Invalid or expired OTP. Please try again.');
            return;
        }
        if (verifyResult.errorCode === 'SESSION_EXPIRED') {
            callbacks.onError?.('Signup session expired. Please start again.');
            sessionStorage.removeItem(STORAGE_KEY_SIGNUP);
            return;
        }
        callbacks.onError?.(verifyResult.error);
        return;
    }

    // Then, finalize signup
    const finalizeResult = await apiPost('/api/auth/customer/signup/finalize', {
        email,
    });

    if (finalizeResult.error) {
        if (finalizeResult.errorCode === 'VERIFICATION_INCOMPLETE') {
            callbacks.onError?.('Email or mobile not verified. Please complete verification.');
            return;
        }
        callbacks.onError?.(finalizeResult.error);
        return;
    }

    // Clear temporary storage
    sessionStorage.removeItem(STORAGE_KEY_SIGNUP);

    // Success! Session cookie is now set by backend
    callbacks.onSuccess?.();
}

/**
 * Helper: Get stored signup data
 */
export function getSignupData(): ManualSignupData | null {
    const data = sessionStorage.getItem(STORAGE_KEY_SIGNUP);
    if (!data) return null;
    try {
        return JSON.parse(data);
    } catch {
        return null;
    }
}

/**
 * Helper: Clear signup data
 */
export function clearSignupData(): void {
    sessionStorage.removeItem(STORAGE_KEY_SIGNUP);
}
